.navbar-wrapper {
	width: 100%;
	height: 75px;
	background: #fafafa;
	box-shadow: 4px 2px 5px rgba(0, 0, 0, 0.33);
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	.logo {
		display: flex;
		flex-direction: row;
		align-self: center;
		margin-left: 20px;
		img {
			width: 30px;
			height: 30px;
		}
		h1 {
			font-size: 24px;
			color: #164a41;
			margin-left: 20px;
		}
	}
	.links {
		margin-right: 20px;
		width: fit-content;
		display: flex;
		align-items: center;
		flex-direction: row;
		a,
		* a {
			font-size: 24px;
			font-weight: 400;
			color: #164a41;
		}
		button {
			margin: 0 20px;
			align-items: center;
			display: flex;
			flex-direction: row;
			border: none;
			background: transparent;
			a {
				margin-left: 5px;
				label {
					margin: 0 5px;
					width: 10px;
					height: 10px;
					border-radius: 50%;
					color: white;
					background: #164a41;
					padding: 0 10px;
				}
			}
			img {
				width: 30px;
				height: 30px;
			}
		}
	}
}
